.header {
    position: fixed;
    background-color: #fff;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    overflow: hidden;
}

.header .nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1240px;
    margin: auto;
    height: 100%;
    padding: 0 3rem;
}
.header .nav img{
    width: 200px;
    height: auto;
}

.header .nav-menu {
    display: flex;
}
.header .nav-item{
    padding: 1rem;
    font-weight: 700;
}

.header .nav-item a:hover {
    padding-bottom: 10px;
    border-bottom: 3px solid var(--primary-dark);
}

.hamburger {
    display: none;
}


@media screen and (max-width: 760px){
    .header{
        max-width: 100%;
    }

    .header .nav{
        max-width: 100%;
        ;;
    }

    .hamburger{
        display: block;
    }

    .nav-menu {
        position: fixed;
        
        left: -100%;
        top: 90px;
        flex-direction: column;
        background-color: rgb(239, 233, 233);
        width: 100%;
        height: 60vh;
        z-index: 999;
        text-align: center;
        transition: .3s;
    }

    .nav-menu.active {
        left: 0;
    }
    .nav-item{
        margin: 1rem;
    }
    .header .nav img{
        width: 150px;
    }
}
.hero{
    background-color: #fff;
    height: 100vh;
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 0 100px;
    margin-top: 20px;
    grid-gap: 2rem;
    gap: 2rem;
}

.hero:before {
    content: '';
    background: url(/static/media/headerbg.aaba6306.png) no-repeat center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 10px;
}
.heroh1{
    color: var(--primary-dark);
    font-size: 3rem;
    font-weight: 800;
    margin-top: 150px;
    
}
.heroh{
    color: var(--primary-dark);
    font-size: 3rem;
    font-weight: 800;
    margin-bottom: 20px;
}


.herop{
font-size: 1.2rem;
font-weight: 700;
margin-left: 10px;
}
.hero img{
    margin-top: 100px;
    width: 400px;
}
.hero .button{
    margin: 1rem;
}
.hero .button:hover{
    background-color: #fff;
    cursor: pointer;
}

@media screen and (max-width:940px) {
.hero .content{
grid-template-columns: 1fr;
}
.hero .content{
width: 100%;
}
.hero .content .hero-img{
padding: 0rem;
width: 90vw;
}
.hero-img{
    display: none;
}

}
    
