*{
    margin: 0;
    padding: 0;
    
}

.container {
    width: 100%;
    max-height: 75vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: hidden; 
    overflow-x: hidden; 
}

.app-wrapper {
    background-color: #fff;
    min-width: 350px;
    min-height: 650px;
    padding: 30px;
    box-sizing: border-box;
    border-radius: 5px;
}
.title{
    color: var(--primary-dark);
    text-align: center;
    margin: 100px 0px 20px 0px;
    font-weight: 900;
}
.info{
    text-align: center;
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 20px;
}

.name,
.email,
.phoneNumber {
    margin: 20px;
    color: var(--primary-dark);
    font-size: 1rem;
}
.phonenumber{
    margin-bottom: 2rem;
}

.input {
    height: 32px;
    font-size: 1.2rem;
    outline: none;
    width: 100%;
    border-top-style: none;
    border-left-style: none;
    border-right-style: none;
    border-bottom-style: 2px solid #333;
    margin-bottom: 10px;
}
.submit {
    display: flex;
    padding: 10px 0;
    font-size: 1.2rem;
    color: #fff;
    font-weight: 900;
    text-transform: capitalize;
    background: linear-gradient(to right, var(--primary-color), var(--primary-dark)); 
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
    border: none;
    margin: auto;
    outline: none;
    justify-content: center;
    border-radius: 7px;
    cursor: pointer;
  }
.error {
    color: #ff0000;
}

button:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}

.form-success {
