.header {
    position: fixed;
    background-color: #fff;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    overflow: hidden;
}

.header .nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1240px;
    margin: auto;
    height: 100%;
    padding: 0 3rem;
}
.header .nav img{
    width: 200px;
    height: auto;
}

.header .nav-menu {
    display: flex;
}
.header .nav-item{
    padding: 1rem;
    font-weight: 700;
}

.header .nav-item a:hover {
    padding-bottom: 10px;
    border-bottom: 3px solid var(--primary-dark);
}

.hamburger {
    display: none;
}


@media screen and (max-width: 760px){
    .header{
        max-width: 100%;
    }

    .header .nav{
        max-width: 100%;
        ;;
    }

    .hamburger{
        display: block;
    }

    .nav-menu {
        position: fixed;
        
        left: -100%;
        top: 90px;
        flex-direction: column;
        background-color: rgb(239, 233, 233);
        width: 100%;
        height: 60vh;
        z-index: 999;
        text-align: center;
        transition: .3s;
    }

    .nav-menu.active {
        left: 0;
    }
    .nav-item{
        margin: 1rem;
    }
    .header .nav img{
        width: 150px;
    }
}